.input-wrapper {
    background-color: white;
    width: 100%;
    border-radius: 10px;
    height: 2.5rem;
    padding: 0 15px;
    box-shadow: 0px 0px 8px #ddd;
    display: flex;
    align-items: center;
}

input {
    background-color: transparent;
    border: none;
    height: 100%;
    font-size: 1rem;
    width: 100%;
    margin-left: 5px;
}

input:focus {
    outline: none;
}

.results-container {
    margin-top: 10px;
}

.result-row {
    display: flex;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.album-art {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    border-radius: 4px;
}

.song-info {
    display: flex;
    flex-direction: column;
}

.song-title, .song-artist {
    margin: 0;
}
.song-title {
    font-weight: bold;
}
.song-artist {
    font-size: 0.9em;
    color: #555;
}
